.icons{width:4rem;height:4rem;margin:1rem}.iconContainer{background:radial-gradient(circle, rgba(255,255,255,0.748337) 0%, rgba(0,230,255,0.692314) 100%);display:flex;align-items:center;justify-content:center}.effect15{margin:1rem 0}.row{display:flex;justify-content:space-around}

.ih-item {
    position: relative;
    background: radial-gradient(circle, rgba(255,255,255,0.7483368347338936) 0%, rgba(0,230,255,0.6923144257703081) 100%);
    transition: all 0.35s ease-in-out;
  }
  .ih-item,
  .ih-item * {
    box-sizing: border-box;
  }
  .ih-item .hoverdiv {
    color: #333;
  }
  .ih-item .hoverdiv:hover {
    text-decoration: none;
  }
  .ih-item img {
    width: 70%;
    height: 70%;
    display: flex;
    align-self: center;
    justify-self: center;
  }
  
  .ih-item.circle {
    position: relative;
    width: 7rem;
    height: 7rem;
    border-radius: 50%;
  }
  .ih-item.circle .img {
    position: relative;
    width: 7rem;
    height: 7rem;
    border-radius: 50%;
  }
  .ih-item.circle .img:before {
    position: absolute;
    display: block;
    content: '';
    width: 100%;
    height: 100%;
    border-radius: 50%;
    box-shadow: inset 0 0 0 0.1rem rgba(255, 255, 255, 0.6), 0 0.1rem 0.1rem rgba(0, 0, 0, 0.3);
    transition: all 0.35s ease-in-out;
  }
  .ih-item.circle .img img {
    border-radius: 20%;
  }
  .ih-item.circle .info {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: left;
    border-radius: 50%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  .ih-item.circle.effect15 .img {
    opacity: 1;
    visibility: visible;
    transform: scale(1) rotate(0);
    transition: all 0.35s ease-in-out;
  }
  .ih-item.circle.effect15.colored .info {
    background: radial-gradient(circle, rgba(255,255,255,0.7483368347338936) 0%, rgba(0,230,255,0.6923144257703081) 100%);
  }
  .ih-item.circle.effect15 .info {
    background: #333333;
    opacity: 0;
    visibility: hidden;
    transform: scale(0.5) rotate(-720deg);
    transition: all 0.35s ease-in-out 0.3s;
  }
  .ih-item.circle.effect15 .info h3 {
    color: rgb(2, 0, 0);
    text-transform: uppercase;
    position: relative;
    letter-spacing: 1px;
    font-size: 18px;
    margin: 0;
    padding: 2.7rem 0.1rem;
    height: 100%;
    text-shadow: 0 0 1px rgb(27, 2, 2), 0 1px 2px rgba(0, 0, 0, 0.3);
  }
  .ih-item.circle.effect15 .info p {
    color: #bbb;
    padding: 0;
    font-style: italic;
    margin: 0 30px;
    font-size: 1rem;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
  }
  .ih-item.circle.effect15 .hoverdiv:hover .img {
    opacity: 0;
    visibility: hidden;
    transform: scale(0.5) rotate(720deg);
  }
  .ih-item.circle.effect15 .hoverdiv:hover .info {
    opacity: 1;
    visibility: visible;
    transform: scale(1) rotate(0);
  }
  
  
